export default {
	data() {
		return {
			merchantList: [],
			searchID: "",
			searchname: "",
			merchant: {},
			emit: 10,
			loadtext: "上拉加载更多",
		}
	},
	onReachBottom() {
		if(this.emit > this.merchantList.length) {
			return
		}
		this.loadtext = "加载中..."
		this.emit += 10 
		this.__init()
	},
	onLoad() {
		if(this.merchantList < 1) {
			uni.showLoading({
				title: '加载中...',
				mask: true
			});
		}
		console.log(this.merchantList);
		this.__init()
	},
	methods: {
		toshopEarn(item) {
			console.log('item', item);
			uni.setStorageSync('merchant', item);
			uni.navigateTo({
				url: "/pages/shops-earn/shops-earn",
			})
		},
		async __init() {
			this.$H.post("/agent/", {
				user_id: uni.getStorageSync('uid'),
				token: uni.getStorageSync('utoken'),
				opt: "merchant_list",
				slimit: 0,  //列表 起始值
				elimit: this.emit,  //列表  数量
				key_value: (this.searchID || this.searchname)
			}).then((data) => {
				this.merchantList = data.arr;
				uni.hideLoading()
				if(this.merchantList.length < 1) {
					uni.showToast({
						title: "暂无数据",
						icon: "none",
						duration: 2500
					})
				}
				// 恢复加载状态
				this.loadtext = this.merchantList.length < this.emit ? "没有更多了" :  "上拉加载更多"
			}).catch(() => {
				console.log("catch error!!");
			})
		},
		searchbtn() {
			this.__init()
		}
	}
}
